body {
  font-family: "Roboto";
  margin-bottom: 50px;
}

.navbar {
  border-radius: 0rem;
  margin-bottom: 10px;
}

.navbar .nav-link {
  position: relative;
}

.navbar .nav-link .notification-circle {
  position: absolute;
  top: 5px;
  right: -10px;
  border-radius: 100%;
  width: 18px;
  height: 18px;
  display: none;
}

.navbar .avatar {
  background-color: tansparent;
  border-radius: 100%;
  width: 38px;
  box-sizing: border-box;
}

.navbar .avatar:hover {
  cursor: pointer;
}

.navbar .pull-nav-right {
  margin: 0 5px;
  float: right;
}

.drop-right {
  left: auto;
  right: 0;
  margin-right: -5px;
}

.dropdown-item {
  cursor: pointer;
}

.card-icons > a {
  color: black;
  text-decoration: none;
  margin-right: 10px;
}

.card-block {
  padding: 0.5rem 0;
}

p.card-text {
  margin: 10px 0;
  padding: 10px 1.25rem;
  border-top: 1px solid #ccc;
  border-bottom: 1px solid #ccc;
}

.card-item {
  padding: 0 1.25rem;
  position: relative;
}

.card {
  position: relative;
  display: block;
}

span.collapse.in {
  display: inline;
}

ul.dropdown-menu.create-post {
  width: 450px;
  text-align: right;
  padding: 15px 15px 0px;
}

ul.dropdown-menu.create-post textarea {
  resize: vertical;
}

.reposted-post {
  line-height: 10px;
  padding: 5px 20px;
  margin-bottom: 5px;
  margin-top: -8px;
  border-radius: 0px;
}

.reposted-post * {
  font-size: 12px !important;
  font-weight: 600;
}

.reposted-post i {
  color: #4caf50 !important;
}

.reposted-post:after {
  content: "";
  display: block;
  height: 100%;
  width: 7px;
  position: absolute;
  left: 0;
  top: 0;
  background-color: #4caf50;
  z-index: 10;
}

a.createcommunity {
  color: #131313;
  text-decoration: none;
}

button.card-toggle {
  width: 100%;
  height: 37px;
  background: transparent;
  border: none;
  position: absolute;
  bottom: -30px;
  left: 0;
  top: 0;
  z-index: 10;
  cursor: pointer;
  text-align: right;
  padding: 0 15px;
}

button.card-toggle:focus {
  outline: none;
}

h5.setting-block {
  font-size: 1.05rem;
  margin-bottom: 0.2rem;
}

.card-collapse::before {
  content: "";
  width: calc(100% + 30px);
  margin: 9px 0 9px -15px;
  height: 1px;
  display: block;
}

.card-form > label {
  font-size: .9rem;
  font-weight: 600;
  margin-right: 15px;
}

.card-form input {
  display: block;
  width: 100%;
}

.card-form input.btn.btn-primary {
  margin-top: 15px;
}

.post-block {
  padding-bottom: 35px;
  margin-bottom: -1px;
  border-radius: 0;
  border: 1px solid;
  cursor: pointer;
}

.post-block .post-name {
  position: absolute;
  top: 0;
}

.post-block .post-username {
  position: absolute;
  top: 18px;
  font-size: 11px;
}

.post-block .card-text {
  margin-top: 10px;
}

.post-block .post-toggle {
  display: block;
  text-align: center;
  padding: 3px 10px;
  font-size: 11px;
  right: 0;
  bottom: -5px;
}

.post-block .post-text {
  cursor: text;
}

.card-icons {
  position: absolute;
  width: 100%;
  bottom: 0;
  left: 0;
  font-size: 14px;
  padding: 1px 20px;
}

.card-icons span.icon {
  margin: 0px 7px;
  cursor: pointer;
}

.card-icons span.icon:first-child {
  padding-left: 0px;
}

.card-icons span.icon:last-child {
  padding-right: 0px;
}

@media (max-width: 768px) {
  .nav-left {
    display: none;
  }
}

.nav-center {
  position: absolute;
  transform: translateX(-50%);
  left: 50%;
}

@media (min-width: 768px) {
  .nav-center {
    display: none;
  }
}

@media (max-width: 643px) {
  .nav-center {
    transform: translateX(-70%);
  }
}

@media (max-width: 543px) {
  .nav-center {
    transform: translateX(0%);
    left: 85px;
  }
}

.nav-center .nav-item {
  margin: 0px 40px !important;
}

@media (max-width: 643px) {
  .nav-center .nav-item {
    margin: 0px 20px !important;
  }
}

@media (max-width: 543px) {
  .nav-center .nav-item {
    margin: 0px 5px !important;
  }
}

.nav-center .nav-link {
  width: 20px;
  text-align: center;
}

@media (max-width: 768px) {
  .nav-center .nav-link {
    margin-top: -6px;
  }
}

@media (max-width: 543px) {
  .nav-center .nav-link {
    margin-top: 0px;
  }
  .nav-center .nav-link .nav-icon-text {
    display: none;
  }
}

.nav-center .nav-icon-text {
  font-size: 10px;
  transform: translatex(-35%);
  width: 70px;
  text-align: center;
}

.reply-header {
  text-align: center;
  font-size: 20px;
  padding: 30px 0 15px;
  color: #7d7d7d;
  text-shadow: 0px 2px rgba(255, 255, 255, 0.66);
}

body {
  background-color: #e2e2e2;
}

.btn {
  border-radius: 0;
}

#login-container {
  position: absolute;
  width: 100%;
  top: 50%;
  left: 0;
  transform: translateY(-70%);
  background: rgba(0, 0, 0, 0.12);
  padding: 30px 0;
  color: white;
  text-shadow: 1px 1px #737373;
  font-weight: 300;
}

#login-container input:focus {
  border-color: #474747;
}

#login-container .form-control {
  border-radius: 0;
}

#login-container .btn-outline-primary {
  width: 100%;
  color: white;
  border-color: white;
  margin-top: 20px;
}

#register-container {
  position: absolute;
  width: 100%;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  background: rgba(0, 0, 0, 0.12);
  padding: 30px 0;
  color: white;
  text-shadow: 1px 1px #504e4e;
  font-weight: 300;
}

#register-container input:focus {
  border-color: #474747;
}

#register-container .form-control {
  border-radius: 0;
}

#register-container .btn-outline-primary {
  width: 100%;
  color: white;
  border-color: white;
}

.navbar {
  background-color: #36363c;
}

.navbar .nav-link, .navbar .navbar-brand {
  color: #dddddd !important;
}

.navbar .nav-link:hover, .navbar .navbar-brand:hover {
  color: #fff !important;
}

.navbar .nav-link .notification-circle, .navbar .navbar-brand .notification-circle {
  background: #dddddd;
  border: 3px solid #36363c;
}

.navbar button, .navbar i {
  color: #dddddd;
  border-color: #dddddd;
}

.navbar button:hover, .navbar i:hover {
  border-color: #fff;
  background-color: transparent;
}

.navbar button:hover button, .navbar button:hover i, .navbar i:hover button, .navbar i:hover i {
  color: #fff;
}

.post-block {
  background-color: #f8f8f8;
  border-color: #b3b3b3;
}

.post-block .card-icons {
  background: #ededed;
  color: #36363c;
  border-top: 1px solid #dddddd;
}

.post-block .reposted-post {
  background-color: #ededed;
  border-bottom: 1px solid #dddddd;
}

.post-block .reposted-post a {
  color: #36363c !important;
}

.post-block .post-toggle {
  background: #ededed;
  border: 1px solid #b3b3b3;
}

.post-block .post-name {
  color: #36363c;
}

#user-settings .card {
  color: #36363c;
  border-radius: 0;
}

#profile-settings::before {
  content: none;
}

#avatar-settings::before {
  content: none;
}

#banner-settings::before {
  content: none;
}

#delete-account-settings::before {
  content: none;
}

#deleteAccModal input:focus {
  border-color: rgba(0, 0, 0, 0.3);
  outline: none;
}

#register-center {
  border: 2px dashed #b3b3b3;
  border-radius: 20px;
  padding-left: 80px;
  padding-right: 80px;
  margin-top: 100px;
  padding-bottom: 100px;
  padding-top: 50px;
}

#register-center input {
  margin-bottom: 7px;
}

.banner-img {
  height: 33vw;
  background-position: center center;
  background-size: cover;
  position: relative;
  z-index: 1;
  top: -10px;
  left: 0;
}

.banner-img .avatar {
  position: absolute;
  height: 120px;
  width: 120px;
  background-position: center;
  background-size: contain;
  border-radius: 100%;
  bottom: -86px;
  left: 50px;
  z-index: 2;
}

.banner-img .edit {
  font-size: 16px;
  background: rgba(0, 0, 0, 0.17);
  padding: 6px 10px;
  position: absolute;
  top: 0;
  right: 0;
  text-decoration: none;
  font-weight: 300;
  color: rgba(255, 255, 255, 0.72);
}

.sort-ul {
  background: rgba(255, 255, 255, 0.5);
  position: relative;
  top: -10px;
  width: 100%;
  z-index: 0;
  padding-left: 250px;
  margin-bottom: 40px;
}

.sort-ul .sort-li {
  cursor: pointer;
  box-sizing: border-box;
  right: 15px;
  position: relative;
  padding: 1px 15px 1px;
  border-bottom: 5px solid transparent;
  text-align: center;
  display: inline-block;
  text-decoration: none;
  bottom: -5px;
}

.sort-ul .sort-li:hover {
  border-bottom: 4px solid #3d6278;
}

.sort-ul .sort-li .header {
  font-size: 10px;
  letter-spacing: 1px;
  display: block;
  text-transform: uppercase;
  color: grey;
}

.sort-ul .sort-li .number {
  font-size: 23px;
}

.sort-ul .friend-btn {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 50px;
}
